.dmgFielUpload input {
  display: none;
}
.dmgFielUpload {
  background-color: #fff !important;
  border: 1px solid #1a7081;
  border-radius: 10px;
}
.dmgFielUpload label {
  position: relative;
  display: inline-block;
  width: 100%;
  min-height: 36px;
}
.dmgFielUpload label:after {
  content: "Datei hochladen";
  position: absolute;
  right:0;
  background-color: #1a7081;
  font-family: Arial;
  font-size: 17px;
  color: #fff;
  border: 1px solid #1a7081;
  border-radius: 5px;
  padding: 4px 30px;
  cursor: pointer;
}
.dmgFielUpload input {
  position: relative;
  display: none;
  left: -90px;
  width: 100%;
  text-indent: -9999px;
  margin-top: 0 !important;
  outline: none !important;
}
.tooltip {
  position: relative;
  display: inline-block;
}
.tooltip .tooltip-text {
  visibility: hidden;
  width: 320px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 699 !important;
  top: -5px;
  left: 110%;
}
.tooltip .tooltip-text::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}
.tooltip:hover .tooltip-text {
  visibility: visible;
}
.dmg_accordion_container {
  z-index: 200 !important;
  display: none;
}
.dmg_accordion_container.deactivate {
  display: block;
}
.nextStepInMenu {
  background-color: #1a7081;
  color:#fff;
  padding: 4px;
}
.dmg_accordion {
  background-color: #f3f3f3;
  color: #000;
  cursor: pointer;
  height: 35px;
  border-bottom: 1px solid #fff !important;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  padding-left: 8px;
  padding-right: 8px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  z-index: 200 !important;
}
.active, .dmg_accordion:hover {
  background-color: #1a7081;
  color: #fff;
}
.dmg_panel {
  padding-top: 6px;
  padding-left: 12px;
  padding-bottom: 6px;
  display: none;
  background-color: white;
  overflow: hidden;
}
.dmg_accordion:after {
  content: '\002B';
  color: #000;
  font-weight: bold;
  float: right;
  margin-left: 5px;
}
.active:after {
  content: "\2212";
}
.icon-dmg-info {
  color: #1a7081;
}
